import React, { useState, useEffect } from 'react';

function Main() {
    const canvasRef = React.useRef(null)

    useEffect(()=>{
        var canvas = canvasRef.current
        const ctx = canvas.getContext('2d');
        ctx.fillRect(0,0, 150, 100);
    })

    return(
        <div>
            <canvas ref={canvasRef} width={300} height={300}/>
        </div>
        );
}

export default Main